    },
    onMouseLeaveRoutes: function () {
      console.log('onMouseLeaveRoutes')
      this.mouseOverRoutes = false
      this.showMoreRoute = false
    },
    onTapMoreRoute: function (e) {
      console.log('onTapMoreRoute', e)
      this.showMoreRoute = true
      // this.showMoreRoute = !this.showMoreRoute
    },
    onTapPDF: function () {
      window.open(this.pdfUrl)
    },
    getAssetsBasePageUrl: function () {
      var loc = location.href
      var base = loc.split('#')[0]
      return base
    },
    resetBannerIndicators: function () {
      this.bannerHeight = window.aldcontentwidth * 0.92 * 400 / 1144
      // var indicators = document.getElementsByClassName('mint-swipe-indicator')
      // console.log('indicators:', indicators && indicators.length)
      // var that = this
      // if (indicators && indicators.length) {
      //   for (let item of indicators) {
      //     // item.className = item.className + ' point-cursor'
      //     item.addEventListener('click', (event) => {
      //       console.log('item click :', event)
      //       that.$refs.bswipe.prev()
      //     })
      //   }
      // }
    }
  }
}
</script>

